<template>
    <div>
        <div class="inputT">增删组件</div>
        <!--主体区域-->
        <section id="todoapp">
            <!--输入框-->
            <header class="header">
                <h1>小黑记事本</h1>
                <input v-model="inputValue" placeholder="请输入任务" class="new-todo" @keyup.enter="add">
            </header>
            <!--列表区域-->
            <section class="main">
                <ul class="todo-list">
                    <li class="todo" v-for="(item,index) in list" :key="index">
                        <div class="view">
                            <span class="index">{{index+1}}.</span>
                            <label>{{item}}</label>
                            <button class="dextory" @click="remove(index)">删除</button>
                        </div>
                    </li>
                </ul>
            </section>
            <!--统计和清空-->
            <footer class="footer">
                <div class="all" v-if="list.length != 0">信息统计：{{list.length}} 条</div>
                <button class="clear" @click="clear" v-show="list.length != 0">clear清空</button>
                <div style="clear:both;"></div>
            </footer>
        </section>
        <!--底部-->
        <footer class="info"></footer>
    </div>
</template>

<script>
    export default {
        name: "notebook",
        data() {
            return {
                list: ['睡觉觉','吃饭饭','打扑克'],
                inputValue:'好好学习，天天向上'
            }
        },
        methods: {
            add(){  /*数据新增*/
                this.list.push(this.inputValue);
            },
            remove(index){ /*数据移出*/
                console.log("删除");
                console.log(index);
                this.list.splice(index,1);
                console.log(this.list);
            },
            clear(){ /*数据全部移除*/
                this.list = [];
            }
        }
    }
</script>

<style scoped>
    .inputT {
        width: 100%;
        height: 4vh;
        line-height: 4vh;
        color: #fff;
        background: #00dbd6;
        margin: 10px 0;
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    li {
        height: 5vh;
        line-height: 5vh;
        border-bottom: 1px solid #333;
        /*margin: 10px 0;*/
    }

    .index, .view label {
        margin-right: 15px;
    }

    header {
        border-bottom: 1px solid #333;
        padding: 10px 0;
    }
    #todoapp{
        border: 1px solid #222;
        padding: 20px 0;
    }
    .new-todo{
        border: 1px solid #bababa;
        height: 4vh;
        line-height: 4vh;
        width: 90%;
    }
    .all{
        height: 4vh;
        line-height: 4vh;
        float: left;
    }
    .clear{
        float: right;
        height: 4vh;
        line-height: 4vh;
    }
    .footer{
        padding: 10px 0;
    }
    h1{
        margin: 10px;
    }
</style>