<template>
    <div class="testList">
        <div class="Ti">组件使用</div>
        <div class="value">父组件向子组件传值msg：{{msg}}</div>
        <div>
            <span>组件测试：</span>
            <button @click="num">点击 {{count}} 次</button>
        </div>
    </div>
</template>

<script>


    export default {
        name: "test",
        props: {
            msg: String,
            jpeg:Array
        },
        data() {
            return {
                count: 0,
                jp:[],
                // jpeg:[]
            }
        },
        methods: {
            num() {
                let _this = this;
                // console.log(_this.jpeg,'子组件获取到的父组件的值')
                console.log(this.count+1);
                this.count++;
                _this.$emit('count',this.count)
            }
        }
    }
</script>

