<template>
    <div>
        <div class="T">请求传值组件</div>
        <button @click="array">请求组件间传值</button>
        <div class="value" v-for="(item,index) in jpeg" :key="index">父组件向子组件传值jpeg：{{item.id}}</div>
    </div>
</template>

<script>
    /*引入axios使用*/
    import Axios from 'axios';
    export default {
        name: "array",
        props: {
            jpeg:Array
        },
        data(){
            return{
                jp:[],
            }
        },
        methods:{
            array() {
                let _this = this;
                Axios.get('http://localhost:8082/api/getlunbo')
                    .then(function (response) {
                        // handle success
                        _this.jp = response.data.message;
                        console.log(_this.jp,'子组件请求的值');
                        _this.$emit('jp',_this.jp)
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    });
            }
        }
    }
</script>

<style>
.T{
    width: 100%;
    height: 4vh;
    line-height: 4vh;
    color: #fff;
    background: #ffc81e;
    margin: 10px 0;
}
</style>