<template>
    <div class="home">
        <!--头部Header-->
        <div class="header1">title标题</div>
        <!--使用组件 测试-->
        <!--第一个组件-->
        <test msg="成功"></test>
        <!--第二个组件-->
        <test msg="true"></test>
        <!--第三个组件-->
        <test msg="right" @count="countNum"></test>
        <!--第四个组件-->
        <array :jpeg="jpg" @jp="jpp"></array>
        <!--第五个组件-->
        <login></login>
        <!--第六个组件-->
        <notebook></notebook>
    </div>
</template>

<script>
    // @ is an alias to /src
    import "@/assets/Home.css";
    import test from '@/components/test.vue';
    import array from '@/components/array.vue';
    import login from '@/components/login.vue';
    import notebook from '@/components/notebook.vue';
    /*引入axios使用*/
    import Axios from 'axios';
    import Notebook from "../components/notebook";

    export default {
        name: 'Home',
        components: {
            Notebook,
            test,
            array,
            login,
            notebook
        },
        data() {
            return {
                jpg: []
            }
        },
        mounted: function () {
        },
        methods: {
            countNum(data) {
                console.log(data, '打印子组件向父组件传递的数据')
            },
            jpp(data){
                let _this = this;
                Axios.get('http://localhost:8082/api/getlunbo')
                    .then(function (response) {
                        // handle success
                        _this.jpg = response.data.message;
                        console.log(_this.jpg,'父组件请求的值');
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    });
                console.log(data,'jpp的data')
            }
        }
    }
</script>
